import {createStore} from "vuex";

export const store = createStore({
    state() {
        return {
            activeComponent: null,
            lang: null,
            enterText: null,
            entered: false,
        };
    },
    mutations: {
        setActiveLink(state, activeComponent) {
            state.activeComponent = activeComponent;
        },
        setLanguage(state, lang) {
            state.lang = lang;
        },
        setEnterText(state, enterText) {
            state.enterText = enterText;
        },
        setEntered(state, entered) {
            state.entered = entered;
        },
    }
});